.container {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25), 0 -10px 25px inset rgba(0, 0, 0, 0.15);
}

.glass {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: 0.2s ease-out;
  padding: 1.5px;
  mask: linear-gradient(rgb(0, 0, 0) 0px, rgb(0, 0, 0) 0px) content-box exclude, linear-gradient(rgb(0, 0, 0) 0px, rgb(0, 0, 0) 0px);
  box-shadow: rgba(255, 255, 255, 0.5) 0 0 0 0.5px inset, rgba(255, 255, 255, 0.25) 0 1px 3px inset, rgba(0, 0, 0, 0.35) 0 1px 4px;
}

.filter-svg {
  display: none;
}